import React from "react";
import "hui/tab";

export default class HATab extends React.Component {
    static propTypes = {
        className: React.PropTypes.string,
        titleText: React.PropTypes.string,
        icon: React.PropTypes.string,
        section: React.PropTypes.array,
        badgeClass: React.PropTypes.string,
        badgeText: React.PropTypes.string
    };

    constructor(props) {
        super(props);
        this._huiComponent = null;
    }

    componentDidMount() {
        // force web component pick up childNode one more time.
        this._huiComponent.postRender();
    }
    
    handleRef = (c) => {
        this._huiComponent = c;
    };

    render() {
        /* jshint ignore:start */
        return (
            <ha-tab ref={this.handleRef} {...this.props}>
                {this.props.children}
            </ha-tab>
        );
        /* jshint ignore:end */
    }
}
